/* Base styling */
body,
html,
.site-wrapper {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
  color: #333;
}

/* Header styles */
.site-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 40px;
  background-color: rgba(0, 0, 0, 0.7);
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 10;
}

.site-header .logo {
  height: 40px;
}

.site-header nav ul {
  list-style: none;
  display: flex;
  gap: 20px;
  margin: 0;
  padding: 0;
}

.site-header nav a {
  color: #fff;
  text-decoration: none;
  font-weight: bold;
}

/* Hero section */
.hero {
  height: 100vh;
  background-size: cover;
  background-position: center;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  text-align: center;
  padding-top: 80px; /* Offset for fixed header */
}

.hero-content h1 {
  font-size: 3rem;
  margin-bottom: 10px;
}

.hero-content p {
  font-size: 1.2rem;
  max-width: 600px;
  margin: 0 auto;
}

.btn {
  display: inline-block;
  margin-top: 20px;
  padding: 12px 30px;
  background-color: #ff5555;
  color: #fff;
  border-radius: 5px;
  text-decoration: none;
  transition: background-color 0.3s ease;
}

.btn:hover {
  background-color: #e04444;
}

/* Section containers */
.container {
  padding: 80px 40px;
}

/* Screenshot gallery */
.screenshot-gallery {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
}

.screenshot-gallery img {
  width: 30%;
  max-width: 400px;
  border-radius: 8px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

/* Footer */
footer {
  background-color: #f2f2f2;
  padding: 20px;
  text-align: center;
  color: #555;
}